#news {
	width: 100%;
	height: 100vh;
	position: relative;
	padding: 10vw;
	overflow: hidden;
	background-color: beige;

	h1 {
		font: bold 200px/1 'arial';
		color: #fff;
	}
	h2 {
		font: bold 150px/1 'arial';
		color: rgba(0, 0, 0, 0.3);
		position: absolute;
		top: 13vh;
		left: 100px;
	}

	.wrap {
		width: 100%;
		display: flex;
		justify-content: space-between;

		article {
			width: 30%;
			padding: 30px;
			border: 1px solid #333;
		}
	}
}
